import React, { useEffect, useRef } from "react";
import { refreshWidgetData } from "./services/api/refreshService"; 
import useToast from "./hooks/useToast"; 
import useDroppableArea from "./hooks/useDroppableArea"; 
import { MSG_REFRESH_ERROR, MSG_REFRESH_SUCCESS } from "./utils/toastMessages";
import { useSelector } from "react-redux";

const WidgetLifecycle = ({ children }) => {
  const { handleDrop } = useDroppableArea();
  const { showSuccessToast, showErrorToast } = useToast();
  
  const initialDraggedData = useSelector(
    (state) => state.droppedObject.droppedObjectData.initialDraggedData
  );
    // âœ… Store latest dragged data in a ref to prevent stale closures
    const latestDraggedData = useRef(initialDraggedData);
    useEffect(() => {
      latestDraggedData.current = initialDraggedData;
    }, [initialDraggedData]);
  
  useEffect(() => {
    if (!initialDraggedData?.data?.items?.length) {
      console.warn("[WidgetLifecycle] ðŸš¨ Waiting for initialDraggedData to update...");
    } else {
      console.log("[WidgetLifecycle] âœ… Valid initialDraggedData detected:", initialDraggedData);
    }
  }, [initialDraggedData]);

  console.log("initialDraggedData in widget-lifecycle", initialDraggedData);

  const hasOnRefreshRun = useRef(false); // Prevent duplicate onRefresh registrations
  console.log("---[widget lifecycle]--- starts");
  

  useEffect(() => {
    if (!window.widget) return;

    // Prevent multiple event registrations
    if (hasOnRefreshRun.current) {
      console.warn("[WidgetLifecycle] â³ onRefresh is already registered. Ignoring duplicate.");
      return;
    }
    hasOnRefreshRun.current = true; // Mark as registered
    const onRefresh = async () => {
      console.log("[WidgetLifecycle] ðŸ”„ onRefresh triggered!");

      const currentDraggedData = latestDraggedData.current;
    
      if (!currentDraggedData?.data?.items?.length) {
        console.error("[WidgetLifecycle] âš ï¸ initialDraggedData is missing or invalid:", currentDraggedData);
        return; // ðŸš¨ Prevents execution if data is missing
      }
    
      const objectId = currentDraggedData.data.items[0]?.objectId;
      const objectType = currentDraggedData.data.items[0]?.objectType;
    
      if (!objectId || !objectType) {
        console.error("[WidgetLifecycle] âŒ Missing objectId or objectType. Skipping refresh.");
        return;
      }
    
      try {
        const dataItems = currentDraggedData.data.items;
        await refreshWidgetData(dataItems, handleDrop);
        showSuccessToast(MSG_REFRESH_SUCCESS);
      } catch (error) {
        console.error("[WidgetLifecycle] âŒ Error during refresh:", error);
        showErrorToast(MSG_REFRESH_ERROR);
      }
    };
    

    window.widget.addEvent("onRefresh", onRefresh);
    console.log("[WidgetLifecycle] âœ… onRefresh event registered successfully");

// âŒ DO NOT REMOVE onRefresh on unmount to prevent re-adding it unnecessarily
}, []); // ðŸ‘ˆ Empty dependency array ensures it runs **only once**

return null;
};


export default WidgetLifecycle;
